.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* New header css */
/* =========== GOOGLE FONTS =========== */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

:root{
    --navbar-color: #707070;
    --main-color: #1C1C1C;
    --links-color-mobile: #fff;
}

body.dark-theme{
    --navbar-color: #fff;
    --main-color: #fff;
    --links-color-mobile: #1c1c1c;
    
}

*{
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
}

body{
    font-family: 'Poppins';
    background-color: var(--links-color-mobile);
}

.bd-container{
    /* max-width: 968px; */
    width: calc(100% - 3rem);
    margin-left: 1.5rem;
    margin-right: 1.5rem;
}


.l-header{
    width: 100%;
    /* position: fixed; */
    position: inherit;
    top: 0;
    left: 0; 
    z-index: 100;
}

/* ========= NAV ========= */



nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media screen and (min-width: 768px){
    body{
      margin: 0;
    }
  
    .section{
      padding-top: 7rem;
    }

    .l-header{
        display: flex;
        align-items: center;
        justify-content: center;
    }
  
    .nav{
      width: 1000px;
      height: calc(3rem + 1.5rem);
    }

    .nav__menu{
        display: flex;
    }

    .nav__logo{
        color: var(--navbar-color);
        font-weight: 600;
    }
  
    .nav__list{
      display: flex;
      align-items: center;
    }

    .nav__item{
      margin-left: 3rem;
      margin-bottom: 0;
    }

    .nav__link{
        color: var(--navbar-color);
        font-weight: 600;
    }
  
    .nav__toggle{
      display: none;
    }

    .__dark-mode{
        margin-left: 2rem;
        cursor: pointer;
    }

    .bx-sun{
        color: var(--main-color);
    }
  
}

@media screen and (max-width: 768px) {

    .nav{
        z-index: 100;
        margin-top: 1.3em;
    }

    .nav__menu{
        position: fixed;
        top: -100vh;
        right: 0;
        left: 0;
        height: 40vh;
        width: 90%;
        margin: 0 auto;
        padding: 2.5rem 0 0;
        text-align: center;
        background-color: var(--main-color);
        transition: .4s;
        box-shadow: 0 0 4px rgba(0,0,0,.1);
        border-radius:1rem;
        z-index: 100;
    }

    .nav__item{
        margin-bottom: 1rem;
    }

    .nav__menu ul li a{
        color: var(--links-color-mobile);
    }

    .nav__link, .nav__logo, .nav__toggle{
        color: var(--main-color);
        font-weight: 600;
    }

    .nav__link{
        transition: .3s;
    }

    .nav__toggle{
        display: block;
        font-size: 1.3rem;
        cursor: pointer;
        color: var(--main-color);
    }

    .__dark-mode{
        color: var(--links-color-mobile);
        cursor: pointer;
    }

    /* show-menu  */

    .show-menu{
        top: calc(3rem + 1rem);
    }


}


